@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
body {
	background-size: 25%;
	min-height: 130vh;
}

.main {
	background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 70%, rgba(255, 255, 255, 1)),
		url(/static/media/mdb.544b54df.png);
	background-size: 25%;
	background-color: #fdfdfd;

}

.info {
	text-align: center;
	font-size: 1.5rem;
	font-weight: 460;
	margin: 4% 0 6%;
	/* font-family: "Roboto Slab", serif; */
}

@supports (-webkit-text-stroke: 1px black) {
	.heading {
		-webkit-text-stroke: 1px black;
		-webkit-text-fill-color: white;
	}
}

.content {
	margin: auto;
	width: 60%;
	margin-top: 4%;
	font-family: "Montserrat", sans-serif;
}

@media only screen and (max-width: 1200px) {
	.content {
		width: 90%;
		/* margin: auto; */
	}
}

.editor {
	border: 2px solid #446cb3;
	box-shadow: 10px 9px 17px -7px rgba(0,0,0,0.75);
}

.titlebar {
	background-color: #f3f3f4;
	padding: 1%;
	/* padding-left: 2%; */
	padding-bottom: 2%;
	height: 40px;
	width: auto;
}

.dot {
	height: 18px;
	width: 18px;
	text-align: center;
	font-weight: bold;
	color: white;
	display: inline-block;
	border-radius: 50%;
}

#select {
	float: left;
	width: 40%;
	border: none;
}

#filename-input {
	/* border: none; */
	font-family: "Montserrat", sans-serif;
	width: 50%;
	margin-left: 10%;
}

.control-wrapper {
	width: 100%;
	padding: 0 1rem 0 1rem;
	/* text-align: center; */
	margin-top: 5%;
}

/* generic classes */

.d8 {
	width: 80%;
	margin: auto;
}

.logo {
	text-align: center;
	font-weight: bold;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	color: #4b7bec;
	/* stroke-width: 100px; */
	text-shadow: 3px 3px 0 #4b7bec, -1px -1px 0 #4b7bec, 1px -1px 0 #4b7bec, -1px 1px 0 #4b7bec,
		1px 1px 0 #4b7bec;
}

.nav-bar {
	--nav-size: 50px;
	height: var(--nav-size);
	padding: 1.5% 0% 2.5%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-size: 2rem;
	background-color: #446cb3;
	width: 100%;
}

/* .nav-items {
	padding: 0 0 1%;
} */

.nav-item a {
	font-family: "Montserrat", sans-serif;
	font-size: 12px;
	color: #ecf0f1;
}

.nav-items .nav-item {
	list-style-type: none;
	display: inline-block;
	padding: 0px 10px;
}

.nav-items .nav-item a {
	font-weight: 500;
	font-size: 16px;
	text-decoration: none;
}

.nav-items .nav-item a:hover {
	transition-duration: 400ms;
	text-decoration: none;
	color: #f0f0f0f0;
}

.logo {
	color: #fff;
	justify-self: start;
	margin-left: 20px;
	cursor: pointer;
}

.new-btn {
	padding: 9px 20px;
	margin-right: 20px;
	border: none;
	background-color: #3867d6;
	color: white;
	font-family: "Montserrat", monospace;
	cursor: pointer;
	border-radius: 50px;
	outline: none;
}

.new-btn:hover {
	color: white;
	transition-duration: 500ms;
	background-color: #4b7bec;
}

.userinfo-wrapper {
	background-color: #34495e;
	padding: 10px;
}

.nav-button {
	padding: 7px;
	border-radius: 50%;
	cursor: pointer;
	background-color: #227093;
	transition: -webkit-filter 500ms;
	transition: filter 500ms;
	transition: filter 500ms, -webkit-filter 500ms;
}

.nav-button:hover {
	-webkit-filter: brightness(1.2);
	        filter: brightness(1.2);
}

